/*
 * Events default styles
 */

@use "../abstracts" as *;

.event_thumbnail {
  height: 25rem;
  position: relative;

  > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.display-table {
  display: table;
  width: 100%;
  height: 100%;

  .table-cell {
    display: table-cell;
    vertical-align: middle;

    > .event-text {
      h6 i {
        padding-right: 5px;

        &:last-child {
          padding-left: 15px;
        }
      }

      p {
        margin: 1.5rem 0;
      }

      .event-link {
        display: inline-block;
        padding: 0.4em 2em;
        color: $color-theme1;
        background-color: white;
        text-transform: capitalize;
      }
    }
  }
}
